  --secondary-background-color: #f5f4fa;
  --address-link-color: rgba(255, 255, 255, .6);
  --border-line-color: #ececec;
  --border-item-color: #eee;
  --bg-color: #c4c4c4;
  --icon-color: #afb1b8;
  --hero-linear-gradient: linear-gradient(to right, rgba(47, 48, 58, .4), rgba(47, 48, 58, .4));
}

